import { BaseEntity } from '../entities/BaseEntity';
import { IntegrationTypeCode } from '../enums/IntegrationTypeCode';
import { StringKeyValue } from '../common/StringKeyValue';

/* 
   Integration target describes connectivity details of the integrated system
   Definition of target systems is done by the account administrator only
   Account administrator Supervisor roles can define specific integrations per account / folder 
*/
export class IntegrationTarget extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // Integration name 
    public name: string;
 
    // Integration type (SMTP | HTTP | IMMIX | SENTINEL | MILESTONE | GENETEC) 
    public type: IntegrationTypeCode;
 
    // Integration protocol: smtp | http | https 
    public protocol: string;
 
    // Operation: GET | POST | PUT (applicable for http/https protocols) 
    public operation: string;
 
    // Host name or ip address (applicable for all protocols) 
    public host: string;
 
    // Port number (applicable for all protocols) 
    public port: number;
 
    // User (applicable for all protocols) 
    public user: string;
 
    // Password (applicable for all protocols) 
    public password: string;
 
    // URL path (applicable for http/https protocols) 
    public url: string;
 
    // Headers (applicable for http/https protocols) 
    public headers: StringKeyValue[];
 
    // List of recipients (applicable for smtp protocol) 
    public recipients: string[];
 
    // Message subject template (applicable for smtp protocol) 
    public subject: string;
 
    // Message body template (applicable for all protocols) 
    public body: string;
 
    // Is clip required for the integration 
    public clipRequired: boolean;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


