import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';
import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { Coordinate } from '../common/Coordinate';
import { SearchResultObject } from '../common/SearchResultObject';

/* 
   Search result item 
*/
export class SearchResult {
 
    // Searched item id 
    public id: string;
 
    // Numeric item sequence number 
    public sequenceId: number;
 
    // When the object has been detected [Epoch milliseconds Timestamp] 
    public timestamp: number;
 
    // Detected behavior type (UNKNOWN | CROSSING | MOVING | GROUPING | OCCUPANCY ...) 
    public behaviorType: BehaviorTypeCode;
 
    // Detected object type (UNKNOWN | PERSON | VEHICLE | BUS | TRACK ...) 
    public objectType: ObjectTypeCode;
 
    // Description (provided by the search engine) 
    public description: string;
 
    // Is this object was detected by Geo rule or sensor rule 
    public isGeo: boolean;
 
    // Object's geo location (WGS-84 coordinate system). This field is applicable only if the sensor is geo-referenced. 
    public location: Coordinate;
 
    // List of all objects in this search item 
    public objectsInfo: SearchResultObject[];
 
    constructor(id?: string, sequenceId?: number, timestamp?: number, behaviorType?: BehaviorTypeCode, objectType?: ObjectTypeCode, description?: string, isGeo?: boolean, location?: Coordinate, objectsInfo?: SearchResultObject[]) { 
        this.id = id;
        this.sequenceId = sequenceId;
        this.timestamp = timestamp;
        this.behaviorType = behaviorType;
        this.objectType = objectType;
        this.description = description;
        this.isGeo = isGeo;
        this.location = location;
        this.objectsInfo = objectsInfo;
    }

}


