import { BoundingBox } from '../common/BoundingBox';
import { ObjectInstance } from '../common/ObjectInstance';

/* 
   Object description in search result
   Search result item may refer to several object (example: in a single group there may be several people) 
*/
export class SearchResultObject {
 
    // Unique Object Id 
    public objectId: number;
 
    // The detecting sensor Id (empty if the search is based on Geo) 
    public sensorId: string;
 
    // The object's bounding box 
    public boundingBox: BoundingBox;
 
    // Instances of the object in the scene (trajectory) 
    public objectHistory: ObjectInstance[];
 
    constructor(objectId?: number, sensorId?: string, boundingBox?: BoundingBox, objectHistory?: ObjectInstance[]) { 
        this.objectId = objectId;
        this.sensorId = sensorId;
        this.boundingBox = boundingBox;
        this.objectHistory = objectHistory;
    }

}


