import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';
import { RulePolygon } from '../common/RulePolygon';

/* 
   Rule definition for Geo search 
*/
export class GeoRule {
 
    // Rule behavior type: MOVING | CROSSING | OCCUPANCY ... 
    public behaviorType: BehaviorTypeCode;
 
    // Dwell time [seconds] 
    public dwellTime: number;
 
    // Max number of people in group (for Occupancy rule) 
    public maxObjectsInGroup: number;
 
    // Single polygon/line definitions 
    public polygon: RulePolygon;
 
    constructor(behaviorType?: BehaviorTypeCode, dwellTime?: number, maxObjectsInGroup?: number, polygon?: RulePolygon) { 
        this.behaviorType = behaviorType;
        this.dwellTime = dwellTime;
        this.maxObjectsInGroup = maxObjectsInGroup;
        this.polygon = polygon;
    }

}


