import { ApplianceStatusTimestamped } from '../common/ApplianceStatusTimestamped';

/* 
   Appliance status list over time 
*/
export class ApplianceStatusTimeSeries {
 
    // Time series label (optional) 
    public label: string;
 
    // Time series representing the status KPIs per timestamp 
    public data: ApplianceStatusTimestamped[];
 
    constructor(label?: string, data?: ApplianceStatusTimestamped[]) { 
        this.label = label;
        this.data = data;
    }

}


