import { EventCountDataPoint } from '../common/EventCountDataPoint';

/* 
   Event count time series result 
*/
export class EventCountTimeSeries {
 
    // Label of series 
    public label: string;
 
    // Time series representing the data points over time 
    public data: EventCountDataPoint[];
 
    constructor(label?: string, data?: EventCountDataPoint[]) { 
        this.label = label;
        this.data = data;
    }

}


