/* 
   Detected color of object (or partial object) 
*/
export enum ColorCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // No color [1] 
    NONE = 1,
 
    // Unknown color [2] 
    UNKNOWN = 2,
 
    // Multiple colors [3] 
    MULTICOLORED = 3,
 
    // White [4] 
    WHITE = 4,
 
    // Black [5] 
    BLACK = 5,
 
    // Gray [6] 
    GRAY = 6,
 
    // Blue [7] 
    BLUE = 7,
 
    // Brown [8] 
    BROWN = 8,
 
    // Green [9] 
    GREEN = 9,
 
    // Yellow [10] 
    YELLOW = 10,
 
    // Orange [11] 
    ORANGE = 11,
 
    // Red [12] 
    RED = 12,
 
    // Pink [13] 
    PINK = 13,
 
    // Purple [14] 
    PURPLE = 14,
 
    // Cyan [15] 
    CYAN = 15,
 
    // Silver [16] 
    SILVER = 16,
 
}