import { ObjectTypeCode } from '../enums/ObjectTypeCode';

/* 
   Object attributes to search for 
*/
export class SearchObject {
 
    // Object type to search for: UNDEFINED | UNKNOWN | PERSON ... 
    public type: ObjectTypeCode;
 
    // Object identifier (License plate number or Face attributes) 
    public identifier: string;
 
    constructor(type?: ObjectTypeCode, identifier?: string) { 
        this.type = type;
        this.identifier = identifier;
    }

}


