import { StringKeyValue } from '../common/StringKeyValue';

/* 
   Agent integration represents a local agent to third-party interactions.
   The purpose is to connect the agent to another system in the local network
   and perform integration without the dependency with the remote core services in the data center or cloud.
   The supported integrations are:
   - Web-hook: REST API call to http(s) endpoint (GET / POST / PUT)
   - Web-socket: Websocket client endpoint; ws(s) 
*/
export class AgentIntegration {
 
    // Type of integration content: 1 - metadata, 2 - events 
    public content: number;
 
    // Endpoint uri (e.g. https://path/to/endpoint | wss://path/to/server) The agent shall connect (as client) to the uri endpoint and send the specified content Supported endpoints: for REST: http | https, for Web Socket: ws | wss 
    public endpoint: string;
 
    // REST HTTP operation: GET | POST | PUT 
    public operation: string;
 
    // List of HTTP headers to add 
    public headers: StringKeyValue[];
 
    constructor(content?: number, endpoint?: string, operation?: string, headers?: StringKeyValue[]) { 
        this.content = content;
        this.endpoint = endpoint;
        this.operation = operation;
        this.headers = headers;
    }

}


