import { FeatureCode } from '../enums/FeatureCode';
import { AccountRole } from '../common/AccountRole';
import { TreeNode } from '../common/TreeNode';

/* 
   User Account info (for adding new user to the account) 
*/
export class UserAccountInfo {
 
    // Account Id + Role 
    public accountRole: AccountRole;
 
    // Root node of the account hierarchy 
    public rootNode: TreeNode;
 
    // List of features enabled for this account 
    public features: FeatureCode[];
 
    constructor(accountRole?: AccountRole, rootNode?: TreeNode, features?: FeatureCode[]) { 
        this.accountRole = accountRole;
        this.rootNode = rootNode;
        this.features = features;
    }

}


