import { DayOfWeekCode } from '../enums/DayOfWeekCode';
import { EventAction } from '../common/EventAction';

/* 
   Recurrent time frame 
*/
export class Recurrent {
 
    // Start time of the recurrent in ISO-8601 format including time offset YYYY-MM-DDThh:mm:ss+0Z:00 
    public startTime: string;
 
    // End time of the recurrent in ISO-8601 format including time offset YYYY-MM-DDThh:mm:ss+0Z:00 Leave empty for no end time 
    public endTime: string;
 
    // Period in ISO-8601 format (PnYnMnD / PnW) Examples: <ul> <li>P1Y - Every 1 year</li> <li>P2M - Every 2 months</li> <li>P3W - Every 3 weeks</li> <li>P4D - Every 2 months</li> </ul> 
    public period: string;
 
    // Days of week - based on ISO8601 
    public daysOfWeek: DayOfWeekCode[];
 
    // Action definition 
    public action: EventAction;
 
    constructor(startTime?: string, endTime?: string, period?: string, daysOfWeek?: DayOfWeekCode[], action?: EventAction) { 
        this.startTime = startTime;
        this.endTime = endTime;
        this.period = period;
        this.daysOfWeek = daysOfWeek;
        this.action = action;
    }

}


