import { SensorStatusTimestamped } from '../common/SensorStatusTimestamped';

/* 
   Sensor status list over time 
*/
export class SensorStatusTimeSeries {
 
    // Account Id (leave empty if unknown) 
    public accountId: string;
 
    // Sensor Id 
    public sensorId: string;
 
    // Time series label (optional) 
    public label: string;
 
    // Time series representing the status mask per timestamp (label field is ignored) 
    public data: SensorStatusTimestamped[];
 
    constructor(accountId?: string, sensorId?: string, label?: string, data?: SensorStatusTimestamped[]) { 
        this.accountId = accountId;
        this.sensorId = sensorId;
        this.label = label;
        this.data = data;
    }

}


